.page-content{
    width: 100%;
   
    display: inline-block;
    background-color: #363636;
    
}
.search-content{
    color: white;
    width: 100%;
    height: 100vh;
    overflow-y: scroll;
    overflow-x: hidden;
}
.search input{
    border-radius: 0;
    height: 60px;
    font-size: 28px;
    padding-left: 30px;
    background-color: #282828 !important;
    border: none;
}

.search-title{
    
    color: white;
    font-size: 30px;
    margin-top: 20px;
    margin-left: 25px;
}
.search-result{
    margin-top: 20px;
}
.result{
    display: inline-block;
    margin-right: 20px;
    margin-left: 20px;
    color: white;
}
.type , .name , .img{
    text-align:  center;
}
.img-content{
    width: 150px;
    height: 160px;
    
}
.result-track{
    color: white;
}
