
.category-content{
    width: 100%;
    height: 100%;
    display: inline-block;
    background-color: #363636;
}
.list-album{
    display: inline-block;

}
.title{
   
    font-size: 30px;
}
.album-name , .title{
    color: white;
    
}
.album-img-cover{
    margin: auto;
   
    height: 150px;
    width: 100%;
    max-width: 150px;
}

  .img-hover-container:hover .overlay {
    transform: scale(1.1);
    transition-duration: 0.3s;
    color: #ddd;
    cursor: pointer;
    box-shadow: 0px 10px 15px #000000
  }
  .middle {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  .img-hover-container:hover .image {
    opacity: 0.3;
  }
  
  .img-hover-container:hover .middle {
    opacity: 1;
  } 
  .image {
    opacity: 1;
    display: block;
    width: 120px;
    height: 200px;
    transition: .5s ease;
    backface-visibility: hidden;
  } 
  .text {
    /* background-color: #4CAF50; */
    color: white;
    font-size: 25px;
    padding: 16px 32px;
  }
