.footer-content{
    background-color: #8B7D6B;
  
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
}
  .small-logo{
      width: 56px;
      height: 56px;
      
  }
  .logo-img{
    display: inline-block;
    margin-bottom: 10px;
  }
  .title-music{
      margin-left: 10px;
      display: inline-block;
      color: white;
  }
  .status{
      margin-left: 10px;
      color: green;
      display: inline-block;
  }
  .middle-content{
      display: inline-block;
  }
  .button-play {
    text-align: center;
  }
  .button-random, .button-step-backward,.button-forward,.button-play-circle,.button-redo,.button-layer,.button-server,.button-volume,.progress{
      display: inline-block
  }
  .button-step-backward,.button-play-circle,.button-redo,.button-forward,.button-layer,.button-server,.button-volume{
      margin-left: 10px;
     
  }
  .left-footer{
      text-align: left;
  }
  .left-footer , .middle{
      display: inline-block
  }
  .right-footer{
      display: inline-block;
  }
  
  .progress-bar{
    height: 12px;
    position: relative;
    width: 100%;
  }
