.content{
    background-color: #363636;
    width: 100%;
    height: 100%;
    display: inline-block;
 
}
.right-side{
    margin-top: 20px;
   
    width: 100%
    
}
.song-content{
    text-align: center;
    margin-bottom: 20px;
}
span{
    color: white;
}
.title{
    color: white;
    font-size: 30px;
}
.singer{
    color: rgb(99, 61, 72);
}
.more{
    color: white;
}
.playBtn{
    background-color: green;
    color: white;
    border-radius: 999px;
    font-size: 30px;
    width: 200px;
    margin-bottom: 10px;
   
}
.song-content-header, .song-content-body{
    margin-bottom: 20px;
}
.right-content,.right-info-content{
    display: inline-block;

}
.img-header{
    width: 250px;
    height: 250px;
}
.right-content{
    float: right;
}
.list-music{
    list-style-type: none;
    color: white;
    width: 100%;
   display: inline-block;  
}
.icon, .song-time,.song-name{
    display: inline-block;
}
.icon{
    
    float: left;
}
.left-list{
    display: inline-block;
    
}   
.right-list{
     display: inline-block;
    
}
.song-name{
    float: left;
    margin-left: 20px;
}
.song-time {
    float: right;
}
.list-content{
    display: inline-block;
    width: 100%;
    margin-bottom: 15px;
    color: white;
}
 .btn-green{
     color: green
 }
 .btn-white{
    color: white;
 }
.active{
    color: green;
}