}

  .img-hover-container:hover .overlay {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition-duration: 0.3s;
            transition-duration: 0.3s;
    color: #ddd;
    cursor: pointer;
    box-shadow: 0px 10px 15px #000000
  }
  .middle {
    -webkit-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 25%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  .img-hover-container:hover .image {
    opacity: 0.3;
  }
  
  .img-hover-container:hover .middle {
    opacity: 1;
  } 
  .image {
    opacity: 1;
    display: block;
    width: 120px;
    height: 200px;
    -webkit-transition: .5s ease;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  } 
  .text {
    /* background-color: #4CAF50; */
    color: white;
    font-size: 25px;
    padding: 16px 32px;
  }

